.nav-container {
  z-index: 1000;
  background-color: #2e073f;
  border-radius: 20px;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: 15px 30px;
  transition: background-color .3s;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 2px 8px #00000026;
}

.logo-container {
  flex-shrink: 0;
}

.logo {
  cursor: pointer;
  border: 4px solid #fff;
  border-radius: 40px;
  width: auto;
  height: 80px;
  transition: transform .3s;
  box-shadow: 0 2px 6px #0000004d;
}

.logo:hover {
  transform: scale(1.1)rotate(5deg);
  box-shadow: 0 4px 12px #e67e22b3;
}

.items {
  flex-grow: 1;
  justify-content: flex-end;
  display: flex;
}

.nav-ul {
  gap: 70px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav-ul li {
  color: #fff;
  cursor: pointer;
  padding: 5px 0;
  font-size: 1.1rem;
  transition: color .3s;
  position: relative;
}

.nav-ul li:after {
  content: "";
  background-color: #fff;
  width: 0%;
  height: 2px;
  transition: width .3s;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav-ul li:hover {
  color: #fff;
}

.nav-ul li:hover:after {
  width: 100%;
}

@media (width <= 768px) {
  .nav-container {
    flex-direction: column;
    align-items: flex-start;
    padding: 15px 20px;
  }

  .items {
    justify-content: center;
    width: 100%;
    margin-top: 10px;
  }

  .nav-ul {
    flex-direction: column;
    align-items: center;
    gap: 15px;
    width: 100%;
  }

  .nav-ul li {
    font-size: 1.2rem;
  }
}

@media (width <= 480px) {
  .card-div {
    max-width: 90%;
    padding: 20px;
  }

  .card-div h1 {
    font-size: 1.5rem;
  }

  .h3-tag, .h3-tag-visi {
    font-size: 1.1rem;
  }
}

a {
  text-decoration: none;
}

:root {
  --card-bg: #fff;
  --button-bg: #ff6f61;
  --button-hover: #ff4b3e;
  --shadow: 0 8px 40px #2575fc29;
  --font: "Poppins", "Segoe UI", Arial, sans-serif;
}

.Fullwp {
  background-color: #7a1cac;
  border: 4px solid #7a1cac;
  border-radius: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}

body, html, #root {
  height: 100%;
  font-family: var(--font);
  background: var(--primary-gradient);
  margin: 0;
}

.WelcomePage-div {
  justify-content: center;
  align-items: center;
  gap: 100px;
  height: 500px;
  min-height: 60vh;
  margin-top: -50px;
  animation: 1s cubic-bezier(.4, 0, .2, 1) fadeInDown;
  display: flex;
}

.otherDetails-div {
  background: var(--card-bg);
  box-shadow: var(--shadow);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  min-width: 320px;
  padding: 40px 50px;
  animation: 1.2s cubic-bezier(.4, 0, .2, 1) fadeInLeft;
  display: flex;
}

.otherDetails-div h1 {
  color: var(--accent);
  letter-spacing: 2px;
  text-shadow: 0 2px 10px #6a11cb14;
  margin: 0;
  font-size: 2.5rem;
  font-weight: 700;
}

.image-div {
  justify-content: center;
  align-items: center;
  animation: 1.2s cubic-bezier(.4, 0, .2, 1) fadeInRight;
  display: flex;
}

.frontPage-image {
  border-radius: 16px;
  width: 340px;
  max-width: 90vw;
  transition: transform .4s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 8px 32px #2575fc2e;
}

.frontPage-image:hover {
  transform: scale(1.04)rotate(-2deg);
  box-shadow: 0 16px 48px #2575fc47;
}

.description-div {
  background: var(--card-bg);
  max-width: 600px;
  box-shadow: var(--shadow);
  text-align: center;
  border-radius: 18px;
  margin: -20px auto 20px;
  padding: 36px 32px 28px;
  animation: 1.2s cubic-bezier(.4, 0, .2, 1) fadeInUp;
}

.description-div p {
  color: #333;
  margin-bottom: 28px;
  font-size: 1.15rem;
  line-height: 1.6;
}

.description-div h1 {
  color: var(--accent);
  margin-bottom: 18px;
  font-size: 1.5rem;
  font-weight: 600;
}

.description-div input {
  width: 70%;
  max-width: 320px;
  font-size: 1.1rem;
  font-family: var(--font);
  border: 2px solid #e5e5e5;
  border-radius: 10px;
  outline: none;
  margin: 0 auto 18px;
  padding: 13px 16px;
  transition: border-color .3s, box-shadow .3s;
  box-shadow: 0 2px 8px #2575fc0f;
}

.description-div input:focus {
  border-color: var(--accent);
  box-shadow: 0 0 0 3px #6a11cb21;
}

.description-div button {
  background: var(--button-bg);
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  margin-top: 10px;
  padding: 13px 32px;
  font-size: 1.15rem;
  font-weight: 600;
  transition: background .3s, box-shadow .3s, transform .2s;
  box-shadow: 0 6px 18px #ff6f612e;
}

.description-div button:hover, .description-div button:focus {
  background: var(--button-hover);
  outline: none;
  transform: translateY(-2px)scale(1.04);
  box-shadow: 0 10px 28px #ff4b3e38;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translateY(-40px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInLeft {
  from {
    opacity: 0;
    transform: translateX(-40px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeInRight {
  from {
    opacity: 0;
    transform: translateX(40px);
  }

  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@media (width <= 900px) {
  .WelcomePage-div {
    min-height: unset;
    flex-direction: column;
    gap: 24px;
    padding: 30px 0 0;
  }

  .otherDetails-div, .image-div {
    min-width: unset;
    width: 95vw;
    padding: 28px 10px;
  }

  .frontPage-image {
    width: 95vw;
    max-width: 95vw;
  }

  .description-div {
    max-width: 95vw;
    padding: 24px 10px 20px;
  }
}

:root {
