}

.contact-icon {
  fill: #000;
  flex-shrink: 0;
  width: 55px;
  height: 55px;
  transition: fill .3s;
}

.contact-item:hover .contact-icon, .contact-item:focus .contact-icon {
  fill: #fff;
}

.contact-text h3 {
  color: #fff;
