}

.contact-text p {
  color: #fff;
  text-shadow: 0 1px 3px #0000004d;
  margin: 0;
  font-size: 1.1rem;
  line-height: 1.6;
}

.contact-link {
  color: #fff;
  text-decoration: none;
  transition: color .3s;
}

.contact-link:hover, .contact-link:focus {
  color: #fff;
  outline: none;
  text-decoration: underline;
}

@media (width <= 768px) {
  .contact-container {
    flex-direction: column;
