.quiz-container {
  color: #1e1e1e;
  background: linear-gradient(145deg, #f5edf7, #f9f4fb);
  border: 2px solid #762a831a;
  border-radius: 20px;
  max-width: 700px;
  margin: 40px auto;
  padding: 30px;
  font-family: Segoe UI, sans-serif;
  transition: transform .3s, box-shadow .3s;
  animation: .8s ease-out fadeIn;
  box-shadow: 0 10px 30px #2e073f26, 0 1px 5px #2e073f1a;
}

.quiz-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 35px #2e073f33;
}

.options-container {
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
  margin-top: 20px;
  display: grid;
}

.options-container button {
  cursor: pointer;
  z-index: 1;
  background-color: #fff;
  border: 2px solid #0000;
  border-radius: 12px;
  padding: 14px 20px;
  font-size: 1rem;
  transition: all .3s cubic-bezier(.4, 0, .2, 1);
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 6px #2e073f14;
}

.options-container button:before {
  content: "";
  z-index: -1;
  background: linear-gradient(90deg, #0000, #762a831a, #0000);
  width: 100%;
  height: 100%;
  transition: left .5s;
  position: absolute;
  top: 0;
  left: -100%;
}

.options-container button:hover:before {
  left: 100%;
}

.options-container button:hover:not(:disabled) {
  background-color: #f0d7fa;
  border-color: #762a83;
  transform: translateY(-3px);
  box-shadow: 0 6px 12px #762a8326;
}

.options-container button:active:not(:disabled) {
  transform: translateY(1px);
  box-shadow: 0 2px 4px #762a831a;
}

.correct-answer {
  font-weight: bold;
  animation: 1s pulse;
  color: #fff !important;
  background: linear-gradient(145deg, #5fb49c, #4da389) !important;
  border-color: #47a68c !important;
}

.wrong-answer {
  font-weight: bold;
  animation: .5s shake;
  color: #fff !important;
  background: linear-gradient(145deg, #e4572e, #d24a24) !important;
  border-color: #c13e1a !important;
}

.options-container button:disabled {
  opacity: .7;
  cursor: not-allowed;
}

.score-display {
  text-align: center;
  color: #2e073f;
  background-color: #762a8314;
  border-radius: 10px;
  margin-top: 25px;
  padding: 12px;
  font-size: 1.2rem;
  font-weight: 600;
  animation: .8s fadeIn;
}

.navigation-buttons {
  justify-content: space-between;
  margin-top: 35px;
  display: flex;
}

.navigation-buttons button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(145deg, #762a83, #6a2575);
  border: none;
  border-radius: 30px;
  padding: 12px 24px;
  font-size: 1rem;
  transition: all .3s;
  position: relative;
  overflow: hidden;
  box-shadow: 0 4px 8px #762a8333;
}

.navigation-buttons button:after {
  content: "";
  opacity: 0;
  transform-origin: 50%;
  background: #ffffff80;
  border-radius: 100%;
  width: 5px;
  height: 5px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: scale(1)translate(-50%, -50%);
}

.navigation-buttons button:hover:not(:disabled):after {
  animation: 1s ease-out ripple;
}

.navigation-buttons button:hover:not(:disabled) {
  background: linear-gradient(145deg, #8a32a0, #762a83);
  transform: translateY(-3px);
  box-shadow: 0 6px 15px #762a834d;
}

.navigation-buttons button:active:not(:disabled) {
  transform: translateY(1px);
  box-shadow: 0 2px 5px #762a8333;
}

.navigation-buttons button:disabled {
  cursor: not-allowed;
  background: linear-gradient(145deg, #d1a5de, #c99ad5);
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-15px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes expandWidth {
  from {
    width: 0;
  }

  to {
    width: 80px;
  }
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  20%, 60% {
    transform: translateX(-5px);
  }

  40%, 80% {
    transform: translateX(5px);
  }
}

@keyframes ripple {
  0% {
    opacity: .5;
    transform: scale(0);
  }

  20% {
    opacity: .5;
    transform: scale(25);
  }

  100% {
    opacity: 0;
    transform: scale(40);
  }
}

@media (width <= 768px) {
